import React from 'react';
import { Button, ButtonGroup } from 'react-bootstrap';
import { Formik, Field, Form, FieldArray } from 'formik';

const formatTransactionPayload = (values) => {
	return {
		earnings: values.filter((a) => a.mutate === 'EARNINGS'),
		deductions: values.filter((a) => a.mutate === 'DEDUCTIONS'),
	};
};

const TransactionForm = ({
	initialValues,
	addEmployeeTransaction,
	employeeId,
	activeId,
}) => (
	<div>
		<Formik
			initialValues={initialValues}
			validate={() => ({ foo: true })}
			onSubmit={(values) => {
				alert(JSON.stringify(values, null, 2));
			}}
			render={({ values, errors, touched, handleReset }) => {
				return (
					<Form>
						<FieldArray
							name="transactions"
							render={({ insert, remove, push }) => (
								<div>
									{values.transactions &&
										values.transactions.map((friend, index) => (
											<div className="row" key={index}>
												<div className="col-sm-3 offset-sm-2">
													<span>Name</span>
													<Field
														name={`transactions.${index}.name`}
														placeholder="Label e.g Stipend"
														type="text"
														className="form-control mb-4"
													/>

													{errors.transactions &&
														errors.transactions[index] &&
														errors.transactions[index].name &&
														touched.transactions &&
														touched.transactions[index].name && (
															<div className="field-error">
																{errors.transactions[index].name}
															</div>
														)}
												</div>
												<div className="col-sm-3">
													<span>Amount</span>
													<Field
														name={`transactions.${index}.amount`}
														placeholder="Amount e.g 40000"
														type="text"
														className="form-control mb-4"
													/>
													{errors.transactions &&
														errors.transactions[index] &&
														errors.transactions[index].amount &&
														touched.transactions &&
														touched.transactions[index].amount && (
															<div className="field-error">
																{errors.transactions[index].amount}
															</div>
														)}
												</div>
												<div className="col-sm-2">
													<span>+/-</span>
													<Field
														name={`transactions.${index}.mutate`}
														as="select"
														className="form-control mb-4"
													>
														<option value="red">....</option>
														<option value="EARNINGS">Earnings</option>
														<option value="DEDUCTIONS">Deductions</option>
													</Field>
													{errors.transactions &&
														errors.transactions[index] &&
														errors.transactions[index].mutate &&
														touched.transactions &&
														touched.transactions[index].mutate && (
															<div className="field-error">
																{errors.transactions[index].mutate}
															</div>
														)}
												</div>
												<div className="col-sm-2 mt-4">
													<ButtonGroup>
														<Button
															variant="danger"
															onClick={() => {
																if (values.transactions.length > 1)
																	remove(index);
															}}
															style={{ marginRight: '20px' }}
														>
															X
														</Button>
														<Button
															className="secondary"
															onClick={() =>
																push({ name: '', amount: '', mutate: '' })
															}
														>
															+
														</Button>
													</ButtonGroup>
												</div>
											</div>
										))}
								</div>
							)}
						/>
						<div className="row">
							<ButtonGroup className="col-sm-4 offset-sm-2 mb-5">
								<Button
									onClick={(event) => {
										event.preventDefault();
										handleReset();
									}}
									variant="secondary"
									className="mr-3"
								>
									Reset
								</Button>
								<Button
									type="submit"
									variant="primary"
									onClick={(e) => {
										e.preventDefault();
										addEmployeeTransaction(
											employeeId,
											formatTransactionPayload(values.transactions),
											activeId
										);
									}}
								>
									Save
								</Button>
							</ButtonGroup>
						</div>
					</Form>
				);
			}}
		/>
	</div>
);

export default TransactionForm;
