/* -------------------------------------------------------------------------- */
/*                            External Dependencies                           */
/* -------------------------------------------------------------------------- */
import React from 'react';
import styled from 'styled-components';
import { Formik } from 'formik';

/* -------------------------- Internal Dependencies ------------------------- */
import NavLayout from 'components/layout/dashboard-layout/navbar';
import Button from 'components/button';
import Input from 'components/input';

/* --------------------------- Image Dependencies --------------------------- */
import { DashboardSubWrapper } from '../../company-structure';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import {
	selectPensionSetting,
	selectisLoading,
} from 'redux/settings/selectors';
import { addPensionSetting, getPensionSetting } from 'redux/settings/actions';
import { useMount } from 'broad-state';

const PensionSetting = ({
	isLoading,
	addPensionSetting,
	getPensionSetting,
	pension,
}) => {
	useMount(() => {
		getPensionSetting();
	});
	console.log(pension);
	const initialValues = {
		id: pension.id,
		employee_rate: pension.employee_rate,
		employer_rate: pension.employer_rate,
	};
	return (
		<>
			<NavLayout title="Pension Setting" />
			<DashboardSubWrapper className="wrapper-contain">
				<SectionHeadsnItems>
					<FormWrapper>
						<FormContent>
							<div className="row mr-10">
								<h4>Set the Employee/Employer quota for pension</h4>
								<br />
								<br />
								<br />
								<br />
								<div className="col-md-12">
									<Formik
										enableReinitialize
										initialValues={initialValues}
										onSubmit={async (
											{ id, employee_rate, employer_rate },
											resetForm
										) => {
											try {
												const data = { employee_rate, employer_rate };
												if (id) {
													data.id = id;
												}
												await addPensionSetting({ ...data });
											} catch (e) {}
										}}
									>
										{({
											values,
											handleChange,
											handleBlur,
											errors,
											touched,
											handleSubmit,
										}) => (
											<form onSubmit={handleSubmit}>
												<div className="row">
													<div className="col-md-12">
														<Input
															hasStrip
															id="employee_rate"
															name="employee_rate"
															type="text"
															label="Employee Setting"
															placeholder="Enter employee quota"
															value={values.employee_rate}
															onChange={handleChange}
															onBlur={handleBlur}
															errorMessage={errors.employee_rate}
															isInvalid={
																errors.employee_rate && touched.employee_rate
															}
														/>
													</div>
													<div className="col-md-12">
														<Input
															hasStrip
															id="employer_rate"
															name="employer_rate"
															type="text"
															label="Employer Setting"
															placeholder="Enter employer quota"
															value={values.employer_rate}
															onChange={handleChange}
															onBlur={handleBlur}
															errorMessage={errors.employer_rate}
															isInvalid={
																errors.employer_rate && touched.employer_rate
															}
														/>
													</div>
												</div>
												<Button
													type="submit"
													className="btn-soft mr-auto mt-4"
													isLoading={isLoading}
													disabled={isLoading}
												>
													Save Changes
												</Button>
											</form>
										)}
									</Formik>
								</div>
							</div>
						</FormContent>
					</FormWrapper>
				</SectionHeadsnItems>
			</DashboardSubWrapper>
		</>
	);
};

const SectionHeadsnItems = styled.div`
	margin-top: 2rem;
	background: #ffffff;
	justify-content: center;
	padding: 2rem;
`;

const FormWrapper = styled.div`
	margin-top: 5rem;
`;

const FormContent = styled.div`
	width: 80%;
	margin: 0 auto;
`;

const mapStateToProps = createStructuredSelector({
	isLoading: selectisLoading,
	pension: selectPensionSetting,
});
export default connect(mapStateToProps, {
	addPensionSetting,
	getPensionSetting,
})(PensionSetting);
