/* -------------------------------------------------------------------------- */
/*                            External Dependencies                           */
/* -------------------------------------------------------------------------- */
import React, { useRef, useState } from 'react';
import { Dropdown, Table } from 'react-bootstrap';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { useMount } from 'broad-state';
import debounce from 'codewonders-helpers/bundle-cjs/helpers/debounce';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';

/* -------------------------- Internal Dependencies ------------------------- */
import NavLayout from 'components/layout/dashboard-layout/navbar';
import Input from 'components/input';
import AddCategory from './views/add-category';
import AddLeave from './views/add-leave';
import { DashboardSubWrapper } from '../company-structure';
import {
	getAllLeaveCategory,
	getAllLeaves,
	deactivateCategory,
	deactivateLeave,
} from 'redux/leave/actions';

import { TableWrap, TableHead } from 'components/layout/dashboard-layout';
import {
	selectAllCategories,
	selectAllLeaves,
	selectisLoading,
	selectisLoadingCategory,
} from 'redux/leave/selectors';
import Loader from 'components/loader';
import Paginate from 'components/pagination';

/* --------------------------- Image Dependencies --------------------------- */
import Search from 'assets/icons/icon-search.svg';
import { ReactComponent as More } from 'assets/icons/icon-menu-vertical.svg';
import { ReactComponent as Add } from 'assets/icons/icon-add.svg';
import trunc from 'codewonders-helpers/bundle-cjs/helpers/trunc';
import { isEmpty } from 'codewonders-helpers';

/* ----------------------------- Leave PropTypes ---------------------------- */
const propTypes = {
	getAllLeaveCategory: PropTypes.func,
	getAllLeaves: PropTypes.func,

	deactivateCategory: PropTypes.func,
	deactivateLeave: PropTypes.func,
	leaves: PropTypes.object,
	categories: PropTypes.object,
};
const Leave = ({
	getAllLeaveCategory,
	getAllLeaves,
	isLoadingCategory,
	isLoading,
	deactivateCategory,
	deactivateLeave,
	leaves,
	categories,
}) => {
	const [showAddCategory, setAddCategory] = useState({
		show: false,
		edit: false,
		data: {},
	});
	const [showAddLeave, setAddLeave] = useState({
		show: false,
		edit: false,
		data: {},
	});

	const [searchStructure, setStructure] = useState('');
	const previousStructureRef = useRef('');

	useMount(() => {
		getAllLeaveCategory();
		getAllLeaves();
	});

	const searchStructures = debounce(async (value) => {
		if (previousStructureRef.current === value) {
			try {
				// await refetch();
			} finally {
				await Promise.all([getAllLeaveCategory(value), getAllLeaves(value)]);
			}
		}
	}, 500);
	return (
		<>
			<NavLayout title="Leaves" />
			<DashboardSubWrapper className="wrapper-contain">
				<div className="row">
					<div className="col-md">
						<div className="card">
							<div className="card-body">
								<p>Leave categories</p>
								<h3>{categories?.count || '...'}</h3>
							</div>
						</div>
					</div>
					<div className="col-md">
						<div className="card">
							<div className="card-body">
								<p>Leave Types</p>
								<h3>{leaves?.count || '...'}</h3>
							</div>
						</div>
					</div>
					{/* <div className="col-md">
						<div className="card">
							<div className="card-body graph__card">
								<div className="d-flex align-items-center justify-content-center">
									<div>
										<Graph active={32} inactive={100 - 32}></Graph>
									</div>
									<div className="graph__legend">
										<p className="blue">
											<span></span>
											<b>32%</b> of staff on leave
										</p>
									</div>
								</div>
							</div>
						</div>
					</div> */}
					<div className="col-md-6"></div>
				</div>

				<SectionHeadsnItems>
					<Input
						placeholder="search in leaves and leave category..."
						label=""
						background="#E4E6EB"
						icon={Search}
						value={searchStructure}
						onChange={(e) => {
							setStructure(e.target.value);
							previousStructureRef.current = e.target.value;
							searchStructures(e.target.value);
						}}
					/>
					<TableHead className="mt-5">
						<h4>Leave Categories</h4>
						<button
							className="btn btn-light-blue"
							onClick={() =>
								setAddCategory((prev) => {
									return { ...prev, show: true, edit: false };
								})
							}
						>
							<Add className="mr-2" /> Add a new category
						</button>
					</TableHead>
					<TableWrap>
						{' '}
						{!isLoadingCategory ? (
							<Table responsive>
								{!isLoading && !isEmpty(categories?.results) ? (
									<>
										<thead>
											<tr>
												<th>S/N</th>
												<th>Name</th>
												<th>Leave Types</th>
												<th>Description</th>
												<th>Company</th>
												<th>Status</th>
												<th className="text-right">Actions</th>
											</tr>
										</thead>
										<tbody>
											{categories?.results?.map((category, index) => (
												<tr>
													<td>{index + 1}</td>
													<td>
														<Link to={`/dashboard/leaves/${category.id}`}>
															{category.name}
														</Link>
													</td>
													<td>{category.leave_count}</td>
													<td className="w-25">
														{trunc(category?.description, 70)}
													</td>
													<td>
														{category?.company
															?.map((data) => data.name)
															?.join(', ')}
													</td>
													<td>
														<span
															className={`status-${
																category?.is_active ? 'active' : 'inactive'
															}`}
														>
															{category?.is_active ? 'Active' : 'Inactive'}
														</span>
													</td>
													<td className="text-right">
														<Dropdown>
															<Dropdown.Toggle as={More} />

															<Dropdown.Menu>
																{' '}
																<Dropdown.Item
																	as={Link}
																	to={`/dashboard/leaves/${category?.id}`}
																>
																	View
																</Dropdown.Item>
																<Dropdown.Item
																	href="#!"
																	onClick={() =>
																		setAddCategory((prev) => {
																			return {
																				...prev,
																				show: true,
																				edit: true,
																				data: category,
																			};
																		})
																	}
																>
																	Edit
																</Dropdown.Item>
																{category?.is_active && (
																	<Dropdown.Item
																		href="#!"
																		className="red"
																		onClick={(e) => {
																			e.preventDefault();
																			deactivateCategory(category?.id, true);
																		}}
																	>
																		Deactivate
																	</Dropdown.Item>
																)}
															</Dropdown.Menu>
														</Dropdown>
													</td>
												</tr>
											))}
										</tbody>
									</>
								) : (
									<p className="text-center mx-auto mt-5 no-data">
										No Leave Categories Yet
									</p>
								)}
							</Table>
						) : (
							<Loader loadingText="Getting Heads" />
						)}
						<Paginate
							total={categories.total_pages}
							next={categories.next}
							previous={categories.previous}
							getData={getAllLeaves}
							currentPage={categories?.current_page}
							getNext={() => getAllLeaveCategory('', '', categories?.next)}
							getPrevious={() =>
								getAllLeaveCategory('', '', categories?.previous)
							}
						/>
					</TableWrap>{' '}
					<TableHead className="mt-5">
						<div className="d-flex align-items-center">
							<h4 className="mr-3">Leaves ({leaves?.count})</h4>
							<Dropdown className="sorter">
								<Dropdown.Toggle id="dropdown-basic">sort by</Dropdown.Toggle>

								<Dropdown.Menu>
									<Dropdown.Item href="#/action-1">Action</Dropdown.Item>
									<Dropdown.Item href="#/action-2">
										Another action
									</Dropdown.Item>
									<Dropdown.Item href="#/action-3">
										Something else
									</Dropdown.Item>
								</Dropdown.Menu>
							</Dropdown>
						</div>
						<button
							className="btn btn-light-blue"
							onClick={() =>
								setAddLeave((prev) => {
									return { ...prev, show: true, edit: false };
								})
							}
						>
							<Add className="mr-2" /> Add a new leave
						</button>
					</TableHead>
					<TableWrap>
						{!isLoading ? (
							<Table responsive>
								{!isLoading && !isEmpty(leaves?.results) ? (
									<>
										<thead>
											<tr>
												<th>S/N</th>
												<th>Name</th>
												<th>Category</th>
												<th>Description</th>
												<th className="w-15">Entitlement Value</th>
												<th className="w-15">Weekend Apply</th>
												<th className="w-15">Months Prior</th>
												<th className="w-15">Gender</th>
												<th>Status</th>
												<th className="text-right">Actions</th>
											</tr>
										</thead>

										<tbody>
											{leaves?.results?.map((leave, index) => (
												<tr>
													<td>{index + 1}</td>
													<td>{leave?.name}</td>
													<td>{leave?.category?.name}</td>
													<td className="w-25">
														{trunc(leave.description, 70)}
													</td>
													<td>{leave?.entitlement_value}</td>
													<td>{leave?.weekend_apply ? 'Yes' : 'No'}</td>
													<td>{leave?.months_prior}</td>
													<td>
														{leave?.for_female && 'Female'}{' '}
														{leave?.for_female && leave?.for_male ? 'and' : ''}{' '}
														{leave?.for_male && 'Male'}
													</td>
													<td>
														<span
															className={`status-${
																leave?.is_active ? 'active' : 'inactive'
															}`}
														>
															{leave?.is_active ? 'Active' : 'Inactive'}
														</span>
													</td>
													<td className="text-right">
														<Dropdown>
															<Dropdown.Toggle as={More} />

															<Dropdown.Menu>
																<Dropdown.Item
																	href="#!"
																	onClick={() =>
																		setAddLeave((prev) => {
																			return {
																				...prev,
																				show: true,
																				edit: true,
																				data: leave,
																			};
																		})
																	}
																>
																	Edit
																</Dropdown.Item>
																<Dropdown.Item
																	href="#!"
																	className="red"
																	onClick={(e) => {
																		e.preventDefault();
																		deactivateLeave(leave?.id, false);
																	}}
																>
																	Deactivate
																</Dropdown.Item>
															</Dropdown.Menu>
														</Dropdown>
													</td>
												</tr>
											))}
										</tbody>
									</>
								) : (
									<p className="text-center mx-auto mt-5 no-data">
										No Leaves Yet
									</p>
								)}
							</Table>
						) : (
							<Loader loadingText="Getting Items..." />
						)}
						<Paginate
							total={leaves.total_pages}
							next={leaves.next}
							currentPage={leaves?.current_page}
							previous={leaves.previous}
							getData={getAllLeaves}
							getNext={() => getAllLeaves('', '', leaves?.next)}
							getPrevious={() => getAllLeaves('', '', leaves?.previous)}
						></Paginate>
					</TableWrap>
				</SectionHeadsnItems>
			</DashboardSubWrapper>
			<AddCategory
				show={showAddCategory?.show}
				closeModal={() =>
					setAddCategory((prev) => {
						return { ...prev, show: false, data: {} };
					})
				}
				data={showAddCategory.data}
				isEdit={showAddCategory?.edit}
			/>
			<AddLeave
				show={showAddLeave?.show}
				closeModal={() =>
					setAddLeave((prev) => {
						return { ...prev, show: false, data: {} };
					})
				}
				data={showAddLeave.data}
				isEdit={showAddLeave?.edit}
			/>
		</>
	);
};
// const Graph = styled.div`
// 	width: 60px;
// 	height: 60px;

// 	background: ${(props) => ` radial-gradient(white 55%, transparent 41%),
//     conic-gradient(#336EE4 0% ${props?.active}%, rgba(51, 110, 228, 0.15) ${props?.active}% ${props?.inactive}%);`};
// 	margin-right: 1.1rem;
// 	display: inline-block;
// 	border-radius: 50%;
// `;

Leave.propTypes = propTypes;
const SectionHeadsnItems = styled.div`
	margin-top: 4rem;
`;
const mapStateToProps = createStructuredSelector({
	isLoadingHead: selectisLoading,
	leaves: selectAllLeaves,
	isLoadingCategory: selectisLoadingCategory,
	categories: selectAllCategories,
});
export default connect(mapStateToProps, {
	getAllLeaveCategory,
	getAllLeaves,

	deactivateCategory,
	deactivateLeave,
})(Leave);
