import React, { useEffect } from 'react';
import styled from 'styled-components';
import { isEmpty } from 'codewonders-helpers';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { getRemunerationById } from 'redux/payroll/actions';
import {
	selectSingleRemuneration,
	selectisLoading as selectRemunerationLoading,
} from 'redux/payroll/selectors';
import { selectEmployee } from 'redux/employee/selectors';
import Loader from 'components/loader';
import { formatAmount } from 'utils';
import UserPaySlip from './user-payroll';

const PayslipBreakdownDetail = ({
	isLoading,
	remuneration,
	employee,
	showHistory = true,
	getRemunerationById,
}) => {
	useEffect(() => {
		if (employee?.remuneration) {
			getRemunerationById(employee.remuneration?.id);
		}
		// eslint-disable-next-line
	}, []);

	const getCalculationPrefix = (calculation_type) => {
		if (calculation_type === 'PERCENTAGE') {
			return '%';
		}
		return '';
	};

	const hasElementType = (elementType) => {
		return remuneration?.payroll_elements?.filter(
			(element) => element.element_type === elementType
		);
	};

	return (
		<div className="row">
			{isLoading ? (
				<Loader loadingText="Getting Payslip Values" />
			) : (
				<>
					<div className="col-md-12">
						<h5>Payslip Breakdown</h5>
					</div>
					<div className="col-md-6">
						{isEmpty(hasElementType('EARNINGS')) ? (
							<p>No Earnings</p>
						) : (
							<ElementWrapper>
								<DetailText>Earnings</DetailText>
								{hasElementType('EARNINGS')?.map((data) => (
									<ListWrapper>
										<LabelText>{data.name}</LabelText>
										<DetailText>
											{getCalculationPrefix(data.calculation_type) === '%'
												? `${formatAmount(
														(Number(employee.rates_per_month) *
															Number(data.calculation_type_value)) /
															100
												  )} `
												: null}
											<span style={{ fontSize: 12 }}>
												(
												{data.calculation_type_value
													? getCalculationPrefix(data.calculation_type) === '%'
														? `${data.calculation_type_value}%`
														: formatAmount(data.calculation_type_value)
													: 'NOT YET SPECIFIED'}
												)
											</span>
										</DetailText>
									</ListWrapper>
								))}
							</ElementWrapper>
						)}
					</div>
					<div className="col-md-6">
						{isEmpty(hasElementType('DEDUCTIONS')) ? (
							<p>No specified Deductions</p>
						) : (
							<ElementWrapper>
								<DetailText>Deductions</DetailText>
								{hasElementType('DEDUCTIONS')?.map((data) => (
									<ListWrapper>
										<LabelText>{data.name}</LabelText>
										<DetailText>
											{getCalculationPrefix(data.calculation_type) === '%'
												? `${formatAmount(
														(Number(employee.rates_per_month) *
															Number(data.calculation_type_value)) /
															100
												  )} `
												: null}
											<span style={{ fontSize: 12 }}>
												(
												{data.calculation_type_value
													? getCalculationPrefix(data.calculation_type) === '%'
														? `${data.calculation_type_value}%`
														: formatAmount(data.calculation_type_value)
													: 'NOT YET SPECIFIED'}
												)
											</span>
										</DetailText>
									</ListWrapper>
								))}
								{employee.pension_applied && (
									<>
										<ListWrapper>
											<LabelText>Employee Pension:</LabelText>
											<DetailText>
												{employee?.pension?.employee_rate
													? formatAmount(employee.pension.employee_rate)
													: 'N/A'}
											</DetailText>
										</ListWrapper>
										<ListWrapper>
											<LabelText>Employer Pension:</LabelText>
											<DetailText>
												{employee?.pension?.employer_rate
													? formatAmount(employee.pension.employer_rate)
													: 'N/A'}
											</DetailText>
										</ListWrapper>
									</>
								)}
								{employee.tax_applied && (
									<ListWrapper>
										<LabelText>Taxes:</LabelText>
										{/* <DetailText>{formatAmount(employee?.tax)}</DetailText> */}
									</ListWrapper>
								)}
							</ElementWrapper>
						)}
					</div>
					<div className="col-md-6">
						{isEmpty(hasElementType('COMPANY_CONTRIBUTION')) ? (
							<p>No Company Contribution</p>
						) : (
							<ElementWrapper>
								<DetailText>Company Contributions</DetailText>
								{hasElementType('COMPANY_CONTRIBUTION')?.map((data) => (
									<ListWrapper>
										<LabelText>{data.name}</LabelText>
										<DetailText>
											{getCalculationPrefix(data.calculation_type) === '%'
												? `${formatAmount(
														(Number(employee.rates_per_month) *
															Number(data.calculation_type_value)) /
															100
												  )} `
												: null}
											<span style={{ fontSize: 12 }}>
												(
												{data.calculation_type_value
													? getCalculationPrefix(data.calculation_type) === '%'
														? `${data.calculation_type_value}%`
														: formatAmount(data.calculation_type_value)
													: 'NOT YET SPECIFIED'}
												)
											</span>
										</DetailText>
									</ListWrapper>
								))}
							</ElementWrapper>
						)}
					</div>
					<div className="col-md-6">
						{isEmpty(hasElementType('FRINGE_BENEFIT')) ? (
							<p>No Fringe Benefits yet</p>
						) : (
							<ElementWrapper>
								<DetailText>Fringe Benefits</DetailText>
								{hasElementType('FRINGE_BENEFIT')?.map((data) => (
									<ListWrapper>
										<LabelText>{data.name}</LabelText>
										<DetailText>
											{getCalculationPrefix(data.calculation_type) === '%'
												? `${formatAmount(
														(Number(employee.rates_per_month) *
															Number(data.calculation_type_value)) /
															100
												  )} `
												: null}
											<span style={{ fontSize: 12 }}>
												(
												{data.calculation_type_value
													? getCalculationPrefix(data.calculation_type) === '%'
														? `${data.calculation_type_value}%`
														: formatAmount(data.calculation_type_value)
													: 'NOT YET SPECIFIED'}
												)
											</span>
										</DetailText>
									</ListWrapper>
								))}
							</ElementWrapper>
						)}
					</div>
					<div className="col-md-6">
						{isEmpty(hasElementType('PROVISIONS')) ? (
							<p>No Provisions yet</p>
						) : (
							<ElementWrapper>
								<DetailText>Provisions</DetailText>
								{hasElementType('PROVISIONS')?.map((data) => (
									<ListWrapper>
										<LabelText>{data.name}</LabelText>
										<DetailText>
											{getCalculationPrefix(data.calculation_type) === '%'
												? `${formatAmount(
														(Number(employee.rates_per_month) *
															Number(data.calculation_type_value)) /
															100
												  )} `
												: null}
											<span style={{ fontSize: 12 }}>
												(
												{data.calculation_type_value
													? getCalculationPrefix(data.calculation_type) === '%'
														? `${data.calculation_type_value}%`
														: formatAmount(data.calculation_type_value)
													: 'NOT YET SPECIFIED'}
												)
											</span>
										</DetailText>
									</ListWrapper>
								))}
							</ElementWrapper>
						)}
					</div>
					<div className="col-md-6">
						{isEmpty(hasElementType('ADDITIONS')) ? (
							<p>No Additions yet</p>
						) : (
							<ElementWrapper>
								<DetailText>Additions</DetailText>
								{hasElementType('ADDITIONS')?.map((data) => (
									<ListWrapper>
										<LabelText>{data.name}</LabelText>
										<DetailText>
											{getCalculationPrefix(data.calculation_type) === '%'
												? `${formatAmount(
														(Number(employee.rates_per_month) *
															Number(data.calculation_type_value)) /
															100
												  )} `
												: null}
											<span style={{ fontSize: 12 }}>
												(
												{data.calculation_type_value
													? getCalculationPrefix(data.calculation_type) === '%'
														? `${data.calculation_type_value}%`
														: formatAmount(data.calculation_type_value)
													: 'NOT YET SPECIFIED'}
												)
											</span>
										</DetailText>
									</ListWrapper>
								))}
							</ElementWrapper>
						)}
					</div>

					<section className="col-md-12 mt-4">
						{employee?.id && showHistory && <UserPaySlip id={employee.id} />}
					</section>
				</>
			)}
		</div>
	);
};

const ElementWrapper = styled.div`
	background-color: #fff;
	box-shadow: 1px 2px 1px 2px #888888;
	border: 1px solid rgb(224, 224, 224);
	padding: 0.2em;
	margin-bottom: 1em;
`;

const ListWrapper = styled.div`
	display: flex;
	flex-direction: row;
	justify-content: space-between;
`;

const LabelText = styled.p`
	font-size: 14px;
`;

const DetailText = styled.p`
	font-size: 14px;
	font-weight: 700;
`;

const mapStateToProps = createStructuredSelector({
	isLoading: selectRemunerationLoading,
	remuneration: selectSingleRemuneration,
	employee: selectEmployee,
});
export default connect(mapStateToProps, {
	getRemunerationById,
})(PayslipBreakdownDetail);
