/* -------------------------------------------------------------------------- */
/*                            External Dependencies                           */
/* -------------------------------------------------------------------------- */
import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import { Formik } from 'formik';

/* -------------------------- Internal Dependencies ------------------------- */
import Button from 'components/button';
import Input, { ErrorText } from 'components/input';
import Modal, { ModalWrap } from 'components/modal';

import { taxValidation } from 'utils/validation-schema';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import {
	addTaxTable,
	editTaxTable,
	getAllCompanyPolicy,
} from 'redux/company-policy/actions';
import {
	selectAllCompanyPolicy,
	selectisLoadingCompanyPolicy,
	selectisLoadingTaxTable,
} from 'redux/company-policy/selectors';
import { debounce, isEmpty } from 'codewonders-helpers';
import { useMount } from 'broad-state';
import Radio from 'components/radio';
import { MultiSelect } from 'components/select';
/* ---------------------------- AddTax PropTypes --------------------------- */
const propTypes = {
	show: PropTypes.bool,
	closeModal: PropTypes.func,
	isLoading: PropTypes.bool,
	isEdit: PropTypes.bool,
};

const AddTax = ({
	show,
	closeModal,
	isLoading,
	isEdit,
	data,
	getAllCompanyPolicy,
	addTaxTable,
	editTaxTable,
	company_policies,
}) => {
	const state = isEmpty(data)
		? {
				income_from: '',
				income_to: '',
				tax_rate: '',
				is_active: 'active',
				company_policy: '',
		  }
		: {
				...data,
				company_policy: data?.company_policy,
				is_active: data?.is_active ? 'active' : 'inactive',
		  };

	const previousSearchTermRef = useRef('');
	// eslint-disable-next-line
	const search = debounce(async (value) => {
		if (previousSearchTermRef.current === value) {
			try {
				// await refetch();
			} finally {
				await getAllCompanyPolicy(value);
			}
		}
	}, 500);

	useMount(() => {
		if (isEmpty(company_policies?.results)) {
			getAllCompanyPolicy('', 1, '', true);
		}
	});

	const getCompnayPoliciesOptions = () => {
		if (!isEmpty(company_policies?.results)) {
			return company_policies.results.map((data) => {
				return {
					value: data.id,
					label: data.name,
				};
			});
		}
		return [];
	};

	const getDefaultComapnies = (value) => {
		let d;
		let formattedData;
		if (!isEmpty(value)) {
			d = value?.filter((option) => {
				return getCompnayPoliciesOptions()?.filter(
					(data) => data.id === option.value
				);
			});
		}
		if (d) {
			formattedData = d.map((data) => {
				return {
					label: data.name,
					value: data.id,
				};
			});
		}
		return formattedData;
	};
	return (
		<>
			<Modal
				show={show}
				closeModal={closeModal}
				title={isEdit ? 'Edit Tax' : 'Add Tax'}
			>
				<ModalWrap className="row mr-0">
					<div className="col-md-6">
						<Formik
							initialValues={state}
							validationSchema={taxValidation}
							onSubmit={async (
								{ income_from, income_to, tax_rate, company_policy, is_active },
								resetForm
							) => {
								const tax_data = {
									income_from,
									income_to,
									tax_rate,
									company_policy: company_policy.map(
										(item) => item.value || item.id
									),
									is_active: is_active === 'active' || false,
								};
								if (!isEdit) {
									try {
										await addTaxTable?.({ ...tax_data });
										closeModal();
										resetForm({});
									} catch (e) {}
								} else {
									try {
										await editTaxTable?.(data?.id, {
											...tax_data,
											company_policy: company_policy.map(
												(item) => item.value || item.id
											),
										});
										closeModal();
									} catch (e) {}
								}
							}}
						>
							{({
								values,
								errors,
								touched,
								handleChange,
								setFieldTouched,
								setFieldValue,
								handleBlur,
								handleSubmit,
							}) => (
								<form onSubmit={handleSubmit}>
									<div className="row">
										<div className="col-md-6">
											<Input
												hasStrip
												id="income_from"
												name="income_from"
												type="number"
												label="Income from*"
												placeholder="NGN"
												value={values.income_from}
												onChange={handleChange}
												onBlur={handleBlur}
												errorMessage={errors.income_from}
												isInvalid={errors.income_from && touched.income_from}
											/>
										</div>
										<div className="col-md-6">
											<Input
												hasStrip
												id="income_to"
												name="income_to"
												type="number"
												label="Income to"
												placeholder="NGN"
												value={values.income_to}
												onChange={handleChange}
												onBlur={handleBlur}
												errorMessage={errors.income_to}
												isInvalid={errors.income_to && touched.income_to}
											/>
										</div>
										<div className="col-md-6">
											<Input
												hasStrip
												id="tax_rate"
												name="tax_rate"
												type="number"
												label="Set tax rate"
												placeholder="Tax Rate"
												value={values.tax_rate}
												onChange={handleChange}
												onBlur={handleBlur}
												errorMessage={errors.tax_rate}
												isInvalid={errors.tax_rate && touched.tax_rate}
											/>
										</div>
										<div className="col-md-12">
											<label className="mb-3">Status</label>
											<div className="d-flex flex-wrap mb-3">
												<Radio
													name="is_active"
													type="radio"
													checked={values.is_active === 'active'}
													value="active"
													onChange={() => setFieldValue('is_active', 'active')}
												>
													{' '}
													<p className="radio">Active</p>
												</Radio>
												<Radio
													name="is_active"
													type="radio"
													checked={values.is_active === 'inactive'}
													value="inactive"
													onChange={() =>
														setFieldValue('is_active', 'inactive')
													}
												>
													{' '}
													<p className="radio">Inactive</p>
												</Radio>
											</div>
										</div>
										<div className="col-md-12">
											<MultiSelect
												label="Select Company policies*"
												placeholder="Select company policies"
												options={getCompnayPoliciesOptions()}
												onChange={(value) => {
													setFieldValue('company_policy', value);
													setFieldTouched('company_policy', true);
												}}
												value={
													isEdit && !touched.company_policy
														? getDefaultComapnies(values?.company_policy)
														: values.company_policy
												}
												name="company_policy"
											/>

											{errors?.company_policy ? (
												<ErrorText>{errors?.company_policy}</ErrorText>
											) : (
												<></>
											)}
										</div>
									</div>

									<Button
										type="submit"
										className="btn-soft mr-auto mt-4"
										isLoading={isLoading}
										disabled={isLoading}
									>
										{isEdit ? 'Save Changes' : 'Add Tax'}
									</Button>
								</form>
							)}
						</Formik>
					</div>
				</ModalWrap>
			</Modal>
		</>
	);
};

AddTax.propTypes = propTypes;
const mapStateToProps = createStructuredSelector({
	isLoadingCompany: selectisLoadingCompanyPolicy,
	company_policies: selectAllCompanyPolicy,
	isLoading: selectisLoadingTaxTable,
});
export default connect(mapStateToProps, {
	addTaxTable,
	editTaxTable,
	getAllCompanyPolicy,
})(AddTax);
