/* -------------------------------------------------------------------------- */
/*                            External Dependencies                           */
/* -------------------------------------------------------------------------- */
import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Formik } from 'formik';
import { isEqual } from 'lodash';

/* -------------------------- Internal Dependencies ------------------------- */
import Button from 'components/button';
import Input from 'components/input';
import Modal, { ModalWrap } from 'components/modal';
import Radio from 'components/radio';
import TextArea from 'components/textarea';
import { headValidation } from 'utils/validation-schema';
import { createStructuredSelector } from 'reselect';
import {
	addTaxReliefGroup,
	editTaxReliefGroup,
} from 'redux/tax-management/actions';
import { selectisLoadingTaxReliefGroup } from 'redux/tax-management/selectors';
import isEmpty from 'codewonders-helpers/bundle-cjs/helpers/is-empty';
import { useParams } from 'react-router-dom';

/* ---------------------------- AddPayrollCategories PropTypes --------------------------- */
const propTypes = {
	show: PropTypes.bool,
	closeModal: PropTypes.func,
	addTaxReliefGroup: PropTypes.func,
	data: PropTypes.object,
	isLoading: PropTypes.bool,
	isEdit: PropTypes.bool,
};

const AddTaxReliefGroup = ({
	show,
	closeModal,
	isLoading,
	addTaxReliefGroup,
	editTaxReliefGroup,
	isEdit,
	data,
}) => {
	const { id } = useParams();
	const state = isEmpty(data)
		? { name: '', description: '', is_active: 'active' }
		: {
				...data,
				is_active: data?.is_active ? 'active' : 'inactive',
		  };

	return (
		<>
			<Modal
				show={show}
				closeModal={closeModal}
				title={isEdit ? 'Edit Tax Relief Group' : 'Add New Tax Relief Group'}
			>
				<ModalWrap>
					<div className="col-md-6">
						<Formik
							initialValues={state}
							enableReinitialize
							validationSchema={headValidation}
							onSubmit={async ({ name, description, is_active }, resetForm) => {
								if (!isEdit) {
									try {
										await addTaxReliefGroup?.({
											name,
											description,
											is_active: is_active === 'active' || false,
											is_header: true,
										});
										closeModal();
										resetForm({});
									} catch (e) {}
								} else {
									try {
										await editTaxReliefGroup?.(
											data?.id,
											{
												name,
												description,
												is_active: is_active === 'active' || false,
												is_header: true,
											},
											id
										);
										closeModal();
									} catch (e) {}
								}
							}}
						>
							{({
								values,
								errors,
								touched,
								handleChange,
								setFieldValue,
								handleBlur,
								handleSubmit,
							}) => (
								<form onSubmit={handleSubmit}>
									<Input
										hasStrip
										id="name"
										name="name"
										type="text"
										label="Name*"
										placeholder="Enter name of the group"
										value={values.name}
										onChange={handleChange}
										onBlur={handleBlur}
										errorMessage={errors.name}
										isInvalid={errors.name && touched.name}
									/>

									<TextArea
										hasStrip
										id="description"
										name="description"
										label="Description"
										placeholder="Enter description"
										value={values.description}
										onChange={handleChange}
										onBlur={handleBlur}
										errorMessage={errors.description}
										isInvalid={errors.description && touched.description}
									/>
									<label className="mb-3">Status*</label>
									<div className="d-flex flex-wrap">
										<Radio
											name="is_active"
											type="radio"
											checked={values.is_active === 'active'}
											value="active"
											onChange={() => setFieldValue('is_active', 'active')}
										>
											{' '}
											<p className="radio">Active</p>
										</Radio>
										<Radio
											name="is_active"
											type="radio"
											checked={values.is_active === 'inactive'}
											value="inactive"
											onChange={() => setFieldValue('is_active', 'inactive')}
										>
											{' '}
											<p className="radio">Inactive</p>
										</Radio>
									</div>
									<Button
										type="submit"
										className="btn-soft mr-auto mt-4"
										isLoading={isLoading}
										disabled={isLoading || isEqual(values, state)}
									>
										{isEdit ? 'Save Changes' : 'Add Tax Relief Group'}
									</Button>
								</form>
							)}
						</Formik>
					</div>
				</ModalWrap>
			</Modal>
		</>
	);
};

AddTaxReliefGroup.propTypes = propTypes;
const mapStateToProps = createStructuredSelector({
	isLoading: selectisLoadingTaxReliefGroup,
});
export default connect(mapStateToProps, {
	addTaxReliefGroup,
	editTaxReliefGroup,
})(AddTaxReliefGroup);
