/* -------------------------------------------------------------------------- */
/*                            External Dependencies                           */
/* -------------------------------------------------------------------------- */
import React, { useRef, useState } from 'react';
import { Dropdown, Table } from 'react-bootstrap';
import styled from 'styled-components';
import moment from 'moment';
import { debounce, isEmpty } from 'codewonders-helpers';
import { useMount } from 'broad-state';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';

/* -------------------------- Internal Dependencies ------------------------- */
import NavLayout from 'components/layout/dashboard-layout/navbar';
import Input from 'components/input';
import { DashboardSubWrapper } from 'pages/dashboard/company-structure';
import { getAllEmployees } from 'redux/employee/actions';
import { selectAllEmployees, selectisLoading } from 'redux/employee/selectors';
import Loader from 'components/loader';
import Paginate from 'components/pagination';
import { getTrueKeys } from 'utils';
import history from 'utils/history';

/* --------------------------- Image Dependencies --------------------------- */
import Search from 'assets/icons/icon-search.svg';
import { ReactComponent as More } from 'assets/icons/icon-menu-vertical.svg';
import { ReactComponent as Add } from 'assets/icons/icon-add.svg';
import { TableWrap, TableHead } from 'components/layout/dashboard-layout';
import { ReinstateTerminate } from './reinstate-terminate';

const EmployeeList = ({
	noNav = false,
	getAllEmployees,
	isLoading,
	employees,
}) => {
	const [searchStructure, setStructure] = useState('');
	const previousStructureRef = useRef('');

	useMount(() => {
		getAllEmployees();
	});

	const searchStructures = debounce(async (value) => {
		if (previousStructureRef.current === value) {
			try {
				// await refetch();
			} finally {
				await getAllEmployees(value);
			}
		}
	}, 500);

	const [modalState, setModalState] = useState({
		displayModal: false,
		title: '',
		userId: '',
	});

	const handleReinstateTerminate = (userId, displayTitle) => {
		setModalState((state) => ({
			...state,
			userId,
			displayTitle,
			displayModal: true,
		}));
	};

	const handleCloseModal = () => {
		setModalState((state) => ({
			...state,
			displayModal: !modalState.displayModal,
		}));
	};

	const getOpposite = (state) => (state ? 'terminate' : 'reinstate');

	return (
		<>
			{!noNav && <NavLayout title="Employees" isBack />}
			<DashboardSubWrapper className="wrapper-contain">
				<div className="row">
					<div className="col-md">
						<div className="card">
							<div className="card-body">
								<p>No. of Active Employees</p>
								<h3>{employees?.active_employees || '...'}</h3>
							</div>
						</div>
					</div>
					<div className="col-md">
						<div className="card">
							<div className="card-body">
								<p>No. of Terminated Employees</p>
								<h3>{employees?.terminated_employees}</h3>
							</div>
						</div>
					</div>
					<div className="col-md-6" />
				</div>
				<SectionHeadsnItems>
					<TableHead className="mt-5">
						<h4>Employees</h4>
						<button
							className="btn btn-light-blue"
							onClick={() => history.push('/dashboard/employee/add')}
						>
							<Add className="mr-2" /> Add a new employee
						</button>
					</TableHead>
					<TableWrap>
						<div className="table__wrap-header">
							<Input
								placeholder="search in employees..."
								label=""
								background="#E4E6EB"
								icon={Search}
								value={searchStructure}
								onChange={(e) => {
									setStructure(e.target.value);
									previousStructureRef.current = e.target.value;
									searchStructures(e.target.value);
								}}
							/>
						</div>
						{!isLoading ? (
							<Table responsive>
								{!isLoading && !isEmpty(employees?.results) ? (
									<>
										<thead>
											<tr>
												<th>S/N</th>
												<th>Name</th>
												<th>Date Engaged</th>
												<th>Workdays</th>
												<th>Hierarchy</th>
												<th>Status</th>
												<th className="text-right">Actions</th>
											</tr>
										</thead>
										<tbody>
											{employees?.results?.map((employee, index) => (
												<tr>
													<td>{index + 1}</td>
													<td>{`${employee?.first_name} ${employee?.last_name}`}</td>
													<td>
														{employee?.date_engaged
															? moment(employee.date_engaged).format(
																	'YYYY-MM-DD'
															  )
															: 'NO DATE ENGAGED'}
													</td>
													<td>
														{getTrueKeys({
															M: employee?.works_monday,
															T: employee?.works_tuesday,
															W: employee?.works_wednesday,
															Thr: employee?.works_thursday,
															F: employee?.works_friday,
															Sat: employee?.works_saturday,
															Sun: employee?.works_sunday,
														}).toString()}
													</td>
													<td>
														{employee?.hierarchy
															?.map((data) => data.name)
															?.join(', ')}
													</td>
													<td>
														<span
															className={`status-${
																employee?.is_active ? 'active' : 'inactive'
															}`}
														>
															{employee?.is_active ? 'Active' : 'Inactive'}
														</span>
													</td>
													<td className="text-right">
														<Dropdown>
															<Dropdown.Toggle as={More} />
															<Dropdown.Menu>
																<Dropdown.Item
																	href="#!"
																	onClick={() =>
																		history.push(
																			`/dashboard/employee/detail/${employee?.id}`
																		)
																	}
																>
																	View
																</Dropdown.Item>
																<Dropdown.Item
																	href="#!"
																	onClick={() =>
																		history.push(
																			`/dashboard/employee/edit/${employee.id}`
																		)
																	}
																>
																	Edit
																</Dropdown.Item>
																{employee?.is_active && (
																	<Dropdown.Item
																		href="#!"
																		className="red text-capitalize"
																		// onClick={(e) => {
																		// 	history.push('/employee/terminate');
																		// }}
																		onClick={() =>
																			handleReinstateTerminate(
																				employee?.id,
																				getOpposite(employee?.is_active)
																			)
																		}
																	>
																		{getOpposite(employee?.is_active)}
																	</Dropdown.Item>
																)}
															</Dropdown.Menu>
														</Dropdown>
													</td>
												</tr>
											))}
										</tbody>
									</>
								) : (
									<p className="text-center m-auto no-data">No Employees Yet</p>
								)}
							</Table>
						) : (
							<Loader loadingText="Getting Employees..." />
						)}
						<Paginate
							total={employees?.total_pages}
							next={employees?.next}
							currentPage={employees?.current_page}
							previous={employees?.previous}
							getData={getAllEmployees}
							getNext={() => getAllEmployees('', '', employees?.next)}
							getPrevious={() => getAllEmployees('', '', employees?.previous)}
						/>
					</TableWrap>
				</SectionHeadsnItems>
				{modalState.displayModal ? (
					<ReinstateTerminate
						displayModal={modalState.displayModal}
						closeModal={handleCloseModal}
						displayTitle={modalState.displayTitle}
						userId={modalState.userId}
					/>
				) : (
					<></>
				)}
			</DashboardSubWrapper>
		</>
	);
};

const SectionHeadsnItems = styled.div`
	margin-top: 4rem;
`;

const mapStateToProps = createStructuredSelector({
	isLoading: selectisLoading,
	employees: selectAllEmployees,
});
export default connect(mapStateToProps, { getAllEmployees })(EmployeeList);
